.nav {
    width: 300px;
    display: flex;
    justify-content: space-around;

    margin: 20px;
    padding: 15px 30px;

    /*font-weight: 300;*/
    /*letter-spacing: 3px;*/
    line-height: 18px;
    font-size: 15px;
    vertical-align: middle;

}

a{
    font-size: 15px;
    text-transform: uppercase;
    text-decoration: none;
    color: rgba(255, 255, 255, 0.8);
}

a:focus {
    color: rgba(255, 255, 255, 0.98);

}