.contactsBlock {
    min-height: 35vh;
    background: #0b0b13;
    /*overflow: hidden;*/
    /*position: relative;*/
    display: flex;
    flex-wrap: wrap;


}

.contactsCase {
    flex-direction: column;
}

.form {
    width: 50%;
    display: flex;
    flex-direction: column;
}

input{
    color: #fff;
    background: none;
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 0;
    box-shadow: none;
    font-size: 14px;
    height: calc(2em + 0.75rem + 2px);
}

textarea {
    background: none;
}

.buttonContainer {
    text-align: center;
}

.button {
    margin: 10px;
    padding: 0 25px;
    line-height: 42px;
    border: 1px solid transparent;
    font-size: 14px;
    letter-spacing: 2px;
    background: #2fbf71;
    color: #fff;
    box-shadow: 4px 4px #fff;
    width: 100px;

}

button:hover {
    background: #fff;
    color: #2fbf71;
    box-shadow: 4px 4px #2fbf71;
    cursor: pointer;
}

@media screen and (max-width: 420px){
    .form {
        width: 100%;
    }
}