.project {
    width: 400px;
    /*background-color: #919158;*/
    background: #0c0c14;

    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.pic {
    width: 100%;
    height: 200px;
    background-color: green;
    display: flex;
    justify-content: center;
    align-items: center;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.projectHref {
    width: 100%;
    height: 100%;

}

a { display: block; position: relative; }
a:after { content: ''; position: absolute; top: 0; left: 0; right: 0; bottom: 0; background: rgba(0,0,0,.5);  opacity: 0; }
a:before { content: ''; position: absolute; top: 0; left: 0; right: 0; bottom: 0; opacity: 0; }
a:hover:before,
a:hover:after { opacity: 1; }

.title {
    font-size: 21px;
    font-weight: 700;
    line-height: 1.75em;
    margin: 0 0 10px 0;

}

.description {
    margin: 5px 5px 0;
}
