.projectsBlock {
  /*min-height: 100vh;*/
  padding: 100px 0;

  .projectsCase {
    flex-direction: column;
    justify-content: flex-start;
    flex-wrap: nowrap;

    .projects {
      width: 100%;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      margin-top: 10px;
      @media screen and (max-width: 875px) {
        justify-content: center;
      }
    }
  }

}

