.title {
    margin: 0 0 30px;
    /*font-family: 'Rubik', sans-serif;*/
    font-weight: 300;
    font-size: 20px;
    line-height: 1.25em;
    letter-spacing: 5px;
    text-transform: capitalize;
    padding: 15px;
    position: relative;
}

.title h2 {
    margin: 0;
}

.title h2:before {
    content: '';
    position: absolute;
    background: #2fbf71;
    width: 55px;
    height: 1px;
    bottom: 0;
    /*left: 50%;*/
    /*transform: translate(-50%, 50%);*/
}

.title h2:after {
    content: "";
    width: 7px;
    height: 7px;
    border-radius: 50%;
    position: absolute;
    bottom: -3px;
    left: 66px;
    right: 0;
    background: #2fbf71;
}



